@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
button {
  padding: .5em 3em;
  background-color: hsla(194, 40%, 20%, .8);
  color: hsla(194, 0%, 100%, .95);
  font-size: 1.2em;

  border: none;
  box-shadow: 0 0 .6em hsla(194, 0%, 0%, .2);
  border-radius: 5px;
  text-align: center;
  text-shadow: 0 0 .3em hsla(194, 0%, 0%, 1);
}

button:hover {
  background-color: hsla(194, 40%, 30%, .8);
  text-shadow: 0 0 1em hsla(194, 0%, 100%, 1);
}

button[disabled],
button[disabled].Secondary {
  background-color: hsla(194, 10%, 20%, .1);
  color: hsla(194, 0%, 100%, .95);
  box-shadow: none;
  text-shadow: none;
  cursor: not-allowed;
}

button[disabled]:hover,
button[disabled]:hover.Secondary {
  background-color: hsla(194, 10%, 20%, .1);
}

.Small {
  padding: .3em 1.2em;
  font-size: .9em;
}

.Secondary {
  background-color: hsla(194, 40%, 20%, .45);
  text-shadow: 0 0 .3em hsla(194, 0%, 0%, .8);
  box-shadow: none;
}

.Secondary:hover {
  background-color: hsla(194, 40%, 25%, .45);
  box-shadow: none;
}
.Clickable {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

.Clickable:hover {
  text-shadow: 0 0 1em hsla(194, 0%, 100%, 1);
}
.Home {
  display: grid;
  height: 100%;
  grid-template-rows: 1fr auto;
  grid-row-gap: 2em;
  row-gap: 2em;
}

.Home button {
  width: 100%;
}

.Main {
  align-self: center;
}

.Footer {
  font-size: .8em;
}

.Footer ul {
  padding-left: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.Footer li {
  list-style-type: none;
}
.Content {
  grid-area: content;

  margin: 3em 0;

  display: flex;
  flex-direction: column;

  width: clamp(1em, 100%, 40em);
}

.Content a {
  text-decoration: none;
  font-weight: 500;
  color: hsla(194, 0%, 0%, 1);
}

.Content a:hover {
  text-decoration: none;
  color: hsla(194, 0%, 40%, 1);
}

.Main {
  padding: 0 1.5em;
}
.Modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0,0,0,.3);

  display: grid;
  place-items: center;
}

.Modal > div {
  width: clamp(0em, 100%, 20em);
  background-color: rgba(255,255,255,1);
  box-shadow: 0 0 5em rgba(0,0,0,.6);
}

.Dialog {
  font-size: 1em;
  margin: 1.5em;
  text-shadow: none;

  display: flex;
  flex-direction: column;
}

.Dialog > * {
  margin: .5em 0;
}

.Dialog input[type=text] {
  border: 0px solid black;
  border-bottom-width: 1px;
  padding: .5em 1em;
  font-size: 1em;
}

.Dialog input[type=text]:focus {
  outline: none;
}

.Dialog .buttons {
  display: flex;
  margin-top: 2em;
  flex-direction: row-reverse;
}

.Dialog .buttons button {
  margin-left: .5em;
}

.Dialog .buttons button:last-child {
  margin-left: 0;
}
header {
  grid-area: header;
  height: 2.5em;
  width: 100%;

  display:  flex;
  justify-content: space-between;

  background-color: hsla(194, 40%, 20%, .8);

  text-shadow: 0 0 .4em hsla(194, 0%, 0%, .5);
  box-shadow: 0 0 2em hsla(194, 0%, 0%, .6);

  white-space: nowrap;
}

.Left,
.Right {
  display: flex;
  flex-direction: row;
}

.Left {
  justify-content: flex-start;
}

.Right {
  justify-content: flex-end;
}

.Item {
  padding: .3em 1.2em;
  border: 0px solid hsla(194, 0%, 100%, .1);

  border-left-width: 1px;

  color: hsla(194, 0%, 100%, .95);
  font-size: 1em;

  align-self: center;
}

.Item:hover img {
  -webkit-filter: drop-shadow(0 0 .4em hsla(194, 0%, 100%, 1));
  filter: drop-shadow(0 0 .4em hsla(194, 0%, 100%, 1));
}

.Title {
  display: flex;

  padding: 0 2em;
  border-left-width: 0px;
  border-right-width: 0px;
  text-decoration: none;
}

@media (max-width: 40em) {
  .Title span {
    display: none !important;
  }
}

.Title img {
  padding-right: .8em;
  height: 2em;
}

.Title span {
  align-self: center;
  font-weight: 800;
  font-size: 1.2em;
}

.Name {
  color: hsla(194, 0%, 100%, .7);
  border-left-width: 0px;
  border-right-width: 1px;

  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 15em;
}

.Name em {
  color: hsla(194, 0%, 100%, 1);
  font-style: normal;
  padding-left: .3em;
}

.Share {
  padding: .8em;
  border-width: 0;
}

img {
  height: 1em;
  -webkit-filter: drop-shadow(0 0 .4em hsla(194, 0%, 0%, .5));
  filter: drop-shadow(0 0 .4em hsla(194, 0%, 0%, .5));
}
.Controller {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.RollCounter {
}

.RollCounter {
  width: 12em;
  height: 1.5em;
  display: grid;
  grid-column-gap: 1px;
  -webkit-column-gap: 1px;
          column-gap: 1px;
  grid-template-columns: 1fr 1fr 1fr;
}

/* background of the bits - 200% width, left side is blue, right is opaque
   right side shows by default, when active shows the left side */
.RollCounter div {
  float: left;
  background: linear-gradient(to right, hsla(194, 40%, 20%, .3) 50%, hsla(194, 0%, 0%, 0) 50%);
  background-size: 200% 100%;
  background-position:right bottom;
}

/* animation for the roll class changes (otherwise it's instantaneous) */
.RollCounter[data-roll="1"] div:nth-child(1),
.RollCounter[data-roll="2"] div:nth-child(2),
.RollCounter[data-roll="3"] div:nth-child(3) {
  transition:all .05s ease-in;
}

.RollCounter[data-roll="1"] div:nth-child(1),
.RollCounter[data-roll="2"] div:nth-child(1),
.RollCounter[data-roll="3"] div:nth-child(1) {
  background-position: left bottom;
  border-top-left-radius: .75em;
  border-bottom-left-radius: .75em;
}

.RollCounter[data-roll="2"] div:nth-child(2),
.RollCounter[data-roll="3"] div:nth-child(2) {
  background-position: left bottom;
}

.RollCounter[data-roll="3"] div:nth-child(3) {
  background-position: left bottom;
  border-top-right-radius: .75em;
  border-bottom-right-radius: .75em;
}
.Scores table {
  border: 2px solid black;
  border-collapse: collapse;
  table-layout: fixed;
  width: 100%;
}

.Scores thead {
  height: 2.5em;
}

.Scores th,
.Scores td {
  padding: .4em;
  border: 1px solid black;
  text-align: center;
}

@keyframes blinking {
  0% {
    background-color: transparent;
  }
  50% {
    background-color: yellow;
  }
  100% {
    background-color: transparent;
  }
}

.Scores td.Blinking {
  animation-name: blinking;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

/* first row with the names */
.Scores tr:nth-child(1) th {
  font-weight: bold;
  border-bottom-width: 2px;
}

/* first column with the categories */
.Scores td:nth-child(1),
.Scores th:nth-child(1) {
  font-weight: bold;
  border-right-width: 2px;
  width: 10em;
}

/* separator between upper and lower sections */
.Scores tr:nth-child(7) td {
  border-bottom-width: 2px;
}

/* separator above Total */
.Scores tr:last-child td {
  border-top-width: 2px;
  font-weight: bold;
}

.Highlighted {
  background-color: hsla(194, 70%, 50%, .1);
}

.Suggestion {
  color: #888;
  font-style: italic;
}

.Blinking {
  background-color: #b3e5ff;
}

.Winner::before {
  content: "🏆 ";
}

.Yahtzee {
  font-size: .8em;

  display: flex;
  flex-direction: column;
}

.Yahtzee > div {
  margin: 1em 0;
}
.Dices {
  display: flex;
  justify-content: space-around;
}

.Dice {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px dashed black;
  border-radius: .5em;
  height: 1.5cm;
  width: 1.5cm;
  font-size: 1.5em;
}

.Dice::before {
  width: 90px;
  height: 90px;
}

.Dice.Rolling::before {
  content: url(/static/media/roll.6d4eb6a6.svg);
}

.Dice.Face-1::before {
  content: url(/static/media/face-1.caeafc6c.svg);
}

.Dice.Face-2::before {
  content: url(/static/media/face-2.cce34ef3.svg);
}

.Dice.Face-3::before {
  content: url(/static/media/face-3.66f9abe3.svg);
}

.Dice.Face-4::before {
  content: url(/static/media/face-4.a1f65145.svg);
}

.Dice.Face-5::before {
  content: url(/static/media/face-5.4577d916.svg);
}

.Dice.Face-6::before {
  content: url(/static/media/face-6.7b1ac721.svg);
}

.Dice.Locked {
  border-style: solid;
}
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;

  -webkit-user-select: none;

      -ms-user-select: none;

          user-select: none;
}

#root {
  margin: 0;

  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: auto 1fr;
  grid-template-areas:
    "header"
    "content";

  width: clamp(0em 100% 40em);
  min-height: 100vh;
  justify-items: center;
}

